import React, { Children } from 'react';
import classNames from 'classnames';

const Preview = props => {
  const { lang, className, children } = props;

  const classes = {
    Preview: classNames(`Preview`, className),
  };

  const newChildren = Children.toArray(children).map(child => {
    if (child.props.mdxType === 'PreviewCode') {
      return React.cloneElement(child, { lang });
    }

    return child;
  });

  return <div className={classes.Preview}>{newChildren}</div>;
};

export default Preview;
